






















import Vue from 'vue'
import {Component, Prop} from 'vue-property-decorator';

@Component
export default class NumberPad extends Vue{
    @Prop(Number) value!:number;
    @Prop(String) type!:Type
    output:string = this.value.toString();
    get typeColor(){
      let className = ''
      if(this.type === '-'){
        className = 'minus'
      }else if(this.type === '+'){
        className = 'plus'
      }
      if(Number(this.output) !== 0){
        className = className.concat(' ready')
      }
      return className
    }
    inputContent(event:MouseEvent){
      const button = event.target as HTMLButtonElement;
      const input = button.textContent!;
      if(this.output === '0'){
        if('0123456789'.indexOf(input)>=0){
          this.output = input;
        }else{
          this.output += input;
        }
        return;
      }
      if(!(this.output.indexOf('.')>=0)){
        if(this.output.length === 7){
          if(input=== '.'){
            this.output += input;
            return;
          }else {return;}
        }
      } else{
        if(input === '.'){return;}
        const [,float] =  this.output.split('.')
        if(float.length>=2) {return}
      }
      this.output += input;
    }
    clear(){
      this.output = '0';
    }
    remove(){
      if(this.output.length ===1){
        this.output = '0';
      }else {
        this.output = this.output.slice(0,-1)
      }
    }
    ok(){
      this.$emit('update:value',parseFloat(this.output))
      this.$emit('submit')
      this.output = this.value.toString();
    }
}
